@font-face {
  font-family: 'Roboto Mono', monospace;;
  @import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@100&display=swap');
}

* {
  font-family: 'Roboto Mono', monospace;
  font-weight: 300;
}


body {
  /*  Colors */
  $bg-color: #e4e4e4;
  $dot-color: #afadad;
  
  /* Dimensions */
  $dot-size: 2px;
  $dot-space: 22px;
  
  display: flex;

  height: 100vh;
  justify-content: center;
  align-items: center;
  text-align: center;

  overflow: hidden;

  background: 
    linear-gradient(90deg, $bg-color ($dot-space - $dot-size), transparent 1%) center,
    linear-gradient($bg-color ($dot-space - $dot-size), transparent 1%) center,
    $dot-color;
  background-size: 22px 22px; 

  h1 {
    float: left;
    position: absolute;
    top: 1rem;
    left: 2.5rem;
  }
  
  span {
    position: absolute;
    bottom: 1.5rem;
    right: 2rem;
    
    font-size: 1.1rem;
    text-align: right;
    line-height: 170%;

    strong {
      font-weight: bold;
    }
  }
}